.graph {
	overflow: hidden;
	position: relative;
	background-color: $blue;
	padding-top: 147px;
	@include md {
		overflow: visible;
		padding-top: 60px; }

	&__header {
		position: relative;
		font-family: $montserrat;
		color: $white;
		max-width: 996px;
		margin: auto; }

	&__box {
		position: absolute;
		right: 104px;
		top: -104px;
		.icon {
			width: 96px;
			height: 112px; } }

	&__big-box {
		position: absolute;
		left: -525px;
		top: 54px;
		.icon {
			width: 333px;
			height: 333px; } }

	&__title {
		font-weight: 600;
		font-size: 60px;
		line-height: 72px;
		@include md {
			font-size: 48px; } }

	&__description {
		font-weight: 600;
		font-size: 24px;
		line-height: 48px;
		@include md {
			font-size: 18px; } }

	&__buttons {
		margin-top: 32px;
		margin-right: 25px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin-right: 0; } }

	&__item {}

	&__button {
		width: 137px;
		height: 83px;
		@include md {
			width: 100px;
			height: 62px; } }

	&__content {
		width: 100%;
		height: 564px;
		margin-top: 42px;
		background-color: $dark-blue;
		@include md {
			width: auto;
			margin: 42px 36px 0; } } }

