.hero {
	position: relative;
	height: 1300px;
	background-color: $blue;
	@include md {
		height: 932px; }

	&__background {
		overflow: hidden;
		position: absolute;
		width: 100%;
		height: 100%;
		&:before {
			content: "";
			bottom: 46.5%;
			position: absolute;
			width: 100%;
			height: 50000%;
			transform: skewY(30deg);
			background-color: $cyan;
			@include md {
				bottom: 38.5%; } } }

	&__inner {
		position: relative;
		max-width: 1170px;
		margin: auto; }

	&__box {
		position: absolute;
		left: -260px;
		top: 174px;
		@include md {
			left: -54px;
			top: 154px; }
		.icon {
			width: 116px;
			height: 135px; } }

	&__box-blurred {
		position: absolute;
		right: -96px;
		top: 336px;
		z-index: 1;
		@include md {
			right: -54px;
			top: 300px; }
		.icon {
			filter: blur(15px);
			width: 234px;
			height: 258px; } }

	&__text {
		display: flex;
		max-width: 996px;
		margin: auto;
		position: relative;
		height: 578px;
		@include md {
			height: 460px; } }

	&__image {
		position: absolute;
		height: 820px;
		top: -39px;
		left: -175px;
		z-index: 1;
		@include md {
			height: 660px;
			top: -37px;
			left: -80px; } }

	&__title-wrapper {
		z-index: 1;
		font-family: $montserrat;
		color: $white;
		margin-left: auto;
		text-align: center;
		width: 62%; }

	&__title {
		font-weight: 600;
		font-size: 72px;
		margin-top: 15px;
		line-height: 1.1;
		@include md {
			font-size: 48px; } }

	&__description {
		margin-top: 18px;
		font-weight: 600;
		font-size: 30px;
		@include md {
			font-size: 18px; } }

	&__button {
		display: inline-block;
		margin-top: 28px; }

	&__video {
		position: relative;
		display: flex;
		align-items: center;
		height: 722px;
		background: $white;
		border-radius: 10px;
		padding: 49px;
		@include md {
			padding: 26px;
			height: 472px; } }

	&__video-container {
		display: flex;
		max-width: 1080px;
		flex-grow: 1;
		height: 624px;
		background: $black;
		border-radius: 10px;
		@include md {
			height: 420px; } }

	&__video-play {
		width: 120px;
		height: 120px;
		margin: auto;
		background: url("../../img/play-button.svg") no-repeat center;
		background-size: cover;

		&:hover {
			opacity: 0.7; }

		&:active {
			opacity: 0.85; } } }






