.header {
	font-family: $montserrat;
	color: $white;
	background-color: $cyan;
	padding: 64px 0 115px;
	@include md {
		padding: 45px 0 65px; }

	&__inner {
		font-size: 0;
		margin: auto;
		max-width: 996px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin: 0 60px; } }

	&__menu {
		display: flex;
		align-items: center; }

	&__nav {}

	&__login {
		margin: 0 8px;
		font-weight: 600;
		font-size: 16px; }

	&__lang {} }

//Logo
.logo {
	font-family: $montserrat;
	display: flex;

	&__image {
		display: flex;
		align-items: center;

		.icon {
			width: 43px;
			height: 44px;
			fill: $white;
			@include md {
				width: 33px;
				height: 33px; } } }

	&__text {
		margin-left: 12px; }

	&__title {
		font-weight: 600;
		font-size: 24px;
		@include md {
			font-size: 18px; } }

	&__title--cyan {
		color: $light-cyan; }

	&__description {
		line-height: 0.5;
		font-weight: 400;
		font-size: 13px;
		@include md {
			font-size: 9.75px; } } }

//Navigation
.nav {
	font-family: $montserrat;

	&__list {
		display: flex; }

	&__item {
		margin: 0 17px;
		&:last-child {
			margin-right: 25px; } }

	&__link {
		text-decoration: none;
		color: $white;
		font-weight: 600;
		font-size: 16px;
		&:hover {
			text-decoration: underline; }
		&:active {
			color: rgba($white, 0.7); }
		@include md {
			font-size: 14px; } } }

//Lang
.lang {
	font-family: $montserrat;
	position: relative;
	@include md {
		margin-left: 8px; }

	&__current {
		padding: 10px 24px;
		font-size: 16px;
		color: transparent;
		@include md {
			padding: 0;
			font-size: 14px; } }

	&__list {
		padding: 10px 24px;
		background-color: $white;
		border-radius: 20px;
		left: 0;
		top: 0;
		position: absolute;
		@include md {
			background-color: transparent;
			padding: 0; }

		&:hover {
			.lang__item--hidden {
				display: block; } } }

	&__item {
		font-size: 16px;
		@include md {
			font-size: 14px; }

		&:hover {
			text-decoration: underline; } }

	&__item--hidden {
		display: none; }


	&__link {
		text-decoration: none;
		color: $blue;
		font-weight: 600;
		font-size: 16px; } }
